import axiosInstance from "../interceptor";
//productlist api
export const getbuyerproduct = async (name, offset) => {
  const data = await axiosInstance.get(
    `list/custom-product-list?categorySlug=${name}&limit=10&offset=${offset}`,
  );
  return data?.data;
};

//product count
export const getbuyerproductcount = async (name) => {
  const data = await axiosInstance.get(
    `list/custom-product-list?categorySlug=${name}&count=1`,
  );
  return data?.data;
};

//seller detail api
export const getsellerdetailapi = async (name) => {
  const result = await axiosInstance.get(`vendor-store/vendor-details/${name}`);
  return result?.data;
};

//seller product api
export const getsellerproductapi = async (name) => {
  const result = await axiosInstance.get(
    `vendor-store/vendor-product-list?limit=0&offset=0&count=0&categorySlug=&displayNameUrl=${name}`,
  );
  return result?.data;
};

//product detail api
export const getsingleproduct = async (slug) => {
  const data = await axiosInstance.get(`product-store/productdetail/${slug}`);
  return data?.data;
};

//seo detail api
export const getmetaData = async (slug) => {
  const data = await axiosInstance.get(`seo/product/${slug}`);
  return data?.data;
};

//productfilter
export const getfilter = async (categorySlug, offset) => {
  const data = await axiosInstance.get(`site-filter/${categorySlug}`);
  return data?.data;
};

export const getProductSeo = async (slug) => {
  const data = await axiosInstance.get(`seo/category/${slug}`);
  return data?.data;
};

export const WidgetDetailList = async (slug) => {
  const result = await axiosInstance.get(`list/widget-detail/${slug}?limit=1&offset=0`);
  return result?.data;
};
