// Next
import { notFound } from "next/navigation";
import { headers } from "next/headers";

// API
import {
  getsingleproduct,
  getmetaData,
} from "@/api/serversideaction/productdetail";
import { GetAddonsList } from "@/utilities/cookies";

// Components & SEO Utils
import TopCategoriesServer from "@/Components/TopCategories/server";
import StructuredData from "@/Components/Seo/StructuredData";
import createMetadata from "@/Components/Seo/createMetadata";
import createStructuredData from "@/Components/Seo/createStructuredData";
import { PriceCalculate } from "@/utilities/PriceCalculate";
import axiosInstance from "@/api/interceptor";
import { GetToken } from "@/utilities/cookies";
import ProductDetail from "@/Components/ProductDetail";

// Utilities
async function getStoreSettingsData() {
  return await axiosInstance.get(`settings/store-setting`);
}
export async function generateMetadata(props) {
  const params = await props.params;
  const h = await headers();
  const host = h.get("host");
  const proto = h.get("x-forwarded-proto") || "https";
  const baseUrl = `${proto}://${host}`;
  const defaultImage = `${baseUrl}/img/logo.svg`;

  // Extract slug
  const slug = params?.slug?.length == 2 ? params.slug[1] : params.slug[0];

  try {
    // Addon → SEO enabled?
    const addon = await GetAddonsList();
    const isSeoOn = addon?.seo?.status === 1;

    // Product detail
    const productRes = await getsingleproduct(slug);
    if (!productRes?.data || productRes.message === "Invalid product") {
      return {
        title: "Product Not Found",
        description: `Product with slug ${slug} not found.`,
        keywords: "Product Not Found",
      };
    }

    const product = productRes.data;

    // SEO data
    const seoData = isSeoOn ? await getmetaData(slug) : null;
    const seo = seoData?.data || {};
    const storeSettingsArray = await getStoreSettingsData();
    const storeSettings = storeSettingsArray?.data?.data?.[0] || {};

    // Title
    const title = seo?.metaTagTitle || product?.name || slug || "Product";

    // Description
    const description =
      seo?.metaTagDescription ||
      product?.description?.replace(/<[^>]+>/g, "")?.substring(0, 160) ||
      `Explore ${title}`;

    // Keywords
    const keywords = seo?.metaTagKeyword || `${title}, ${slug}`;

    // Image selection → EXACT LIKE SECOND EXAMPLE
    let imageUrlProd = defaultImage;

    if (
      Array.isArray(product?.productOriginalImage) &&
      product.productOriginalImage.length > 0
    ) {
      const imgs = product.productOriginalImage || [];
      const defaultImg =
        imgs?.find(
          (img) => img?.defaultImage === 1 && img?.containerName?.trim(),
        ) ||
        imgs?.find((img) => img?.containerName?.trim()) ||
        imgs[0];

      if (defaultImg?.containerName && defaultImg?.image) {
        imageUrlProd = `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${defaultImg.containerName}&name=${defaultImg.image}&width=1200&height=630`;
      }
    }

    // Price (same logic as second example)
    const productPrice = PriceCalculate(
      product?.price,
      product?.pricerefer,
      product?.flag,
      product?.taxType,
      product?.taxValue,
    );

    // Currency
    const currency = storeSettings?.currencyCode || "INR";

    // Availability
    const availability =
      product?.stockStatus == "inStock" ? "in stock" : "out of stock";

    // Brand
    const brand = seo.brand || product.brandName || product.brand;

    // Build metadata
    return createMetadata({
      title,
      description,
      keywords,
      imageUrl: imageUrlProd,
      url: `${baseUrl}/product/${slug}`,
      showRobots: true,

      // PRODUCT EXTRA OG TAGS
      price: productPrice,
      currency,
      availability,
      brand,
    });
  } catch (err) {
    console.error("Metadata Error:", err);

    return createMetadata({
      title: slug,
      description: `Explore ${slug}`,
      keywords: slug,
      imageUrl: defaultImage,
      url: `${baseUrl}/product/${slug}`,
      showRobots: true,
    });
  }
}

// -------------------------------------------------------
// PRODUCT PAGE
// -------------------------------------------------------
export default async function ProductPage(props) {
  const params = await props.params;
  const h = await headers();
  const host = h.get("host");
  const proto = h.get("x-forwarded-proto") || "https";
  const baseUrl = `${proto}://${host}`;
  const defaultImage = `${baseUrl}/img/logo.svg`;

  const slug = params?.slug?.length == 2 ? params.slug[1] : params.slug[0];

  // API calls
  const [token, productRes, addon, storeSettingsArray] = await Promise.all([
    GetToken(),
    getsingleproduct(slug),
    GetAddonsList(),
    getStoreSettingsData(),
  ]);

  const storeSettings = storeSettingsArray?.data?.data?.[0] || {};
  if (!productRes?.data || productRes.message === "Invalid product") {
    return notFound();
  }

  const product = productRes.data;

  // -------- IMAGE LOGIC (identical to second example) ----------
  let imageUrlProd = defaultImage;

  if (
    Array.isArray(product?.productOriginalImage) &&
    product.productOriginalImage.length > 0
  ) {
    const imgs = product.productOriginalImage || [];
    const defaultImg =
      imgs?.find(
        (img) => img?.defaultImage === 1 && img?.containerName?.trim(),
      ) ||
      imgs?.find((img) => img?.containerName?.trim()) ||
      imgs[0];

    if (defaultImg?.containerName && defaultImg?.image) {
      imageUrlProd = `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${defaultImg.containerName}&name=${defaultImg.image}&width=1200&height=630`;
    }
  }

  // Price
  const productPrice = PriceCalculate(
    product?.price,
    product?.pricerefer,
    product?.flag,
    product?.taxType,
    product?.taxValue,
  );

  // Currency
  const currency = storeSettings?.currencyCode || "INR";

  // Availability
  const availability =
    product?.stockStatus == "inStock"
      ? "https://schema.org/InStock"
      : "https://schema.org/OutOfStock";

  // Title
  const title = product.metaTagTitle || product.name || slug;

  // Description
  const description =
    product.metaTagDescription ||
    product?.description?.replace(/<[^>]+>/g, "")?.substring(0, 160) ||
    `Explore ${title}`;

  // Brand
  const brand = product.brandName || product.brand;

  // Category
  const category =
    product?.Category?.[0]?.categoryName || product.categoryName || "";
  // ----------- STRUCTURED DATA (FULL PRODUCT) -----------
  const schema = createStructuredData({
    type: "Product",
    title,
    description,
    url: `${baseUrl}/product/${slug}`,
    image: imageUrlProd,
    price: productPrice,
    currency,
    availability,
    brand,
    sku: product?.sku,
    category,
    datePublished: product?.createdDate,
    dateModified: product?.modifiedDate,
    organizationName: host,
    organizationUrl: baseUrl,
    organizationLogo: imageUrlProd,
    ...(product?.rating && {
      aggregateRating: {
        ratingValue: product.rating,
        reviewCount: product.reviewCount || 0,
      },
    }),
  });

  return (
    <>
      <TopCategoriesServer />

      <StructuredData data={schema} />

      <ProductDetail
        token={token}
        rawProduct={productRes?.data}
        slug={slug}
        addonlist={addon}
      />
    </>
  );
}
