export const PriceCalculate = (price, pricerefer, flag, taxType, taxValue) => {
  if (flag !== "") {
    switch (taxType) {
      case 1:
        const priceWithOutTax =
          parseFloat(pricerefer) + parseFloat(Number(taxValue));

        return parseFloat(priceWithOutTax).toFixed(2);

      case 2:
        const percentToAmount = pricerefer * (Number(taxValue) / 100);
        const priceWithTax =
          parseFloat(pricerefer) + parseFloat(percentToAmount);

        return parseFloat(priceWithTax).toFixed(2);

      default:
        return parseFloat(pricerefer);
    }
  } else {
    switch (taxType) {
      case 1:
        const priceWithOutTax = parseFloat(price) + Number(taxValue);
        return parseFloat(priceWithOutTax).toFixed(2);

      case 2:
        const percentToAmount = price * (Number(taxValue) / 100);
        const priceWithTax = parseFloat(price) + percentToAmount;

        return parseFloat(priceWithTax).toFixed(2);
      default:
        return price;
    }
  }
};

export const PriceTaxCalculate = (price, taxType, taxValue) => {
  switch (taxType) {
    case 1:
      const priceWithOutTax = parseFloat(price) + Number(taxValue);
      return parseFloat(priceWithOutTax).toFixed(2);
    case 2:
      const percentToAmount = price * (Number(taxValue) / 100);
      const priceWithTax = parseFloat(price) + percentToAmount;

      return parseFloat(priceWithTax).toFixed(2);
    default:
      return price;
  }
};
